<!--
 :    
Detail :
 * 
examples: 
 *  
-->
<template>
  <div>
    <v-text-field
      v-if="editable"
      :label="label"
      :name="name"
      ref="control"
      v-model="vValue"
      :placeholder="placeholder"
      :append-outer-icon="icon"
      :prepend-icon="prependIcon"
      clearable
      :background-color="backgroundColor"
      :color="color"
      :outline="outline"
      :solo="solo"
      :hide-details="hideDetail"
      :hint="hint"
      :class="customClass"
      :counter="counter"
      :maxlength="maxlength"
      @input="input"
      :error="error"
      :error-messages="errorMsg"
    ></v-text-field>
    <v-text-field
      v-else
      v-model="value"
      :placeholder="value ? null : $t('message.noData')"
      :label="label"
      readonly
      ></v-text-field>
  </div>
</template>

<script>
export default {
  /* attributes: name, components, props, data */
  name: 'y-text',
  $_veeValidate: {
    name () {
      return this.name;
    },
    value () {
      return this.vValue;
    }
  },
  props: {
    name: {
      type: String
    },
    label: {  //     
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: ''
    },
    // TODO :     
    errorMsg: {
      type: String,
      required: false
    },
    //   
    editable: {
      type: Boolean,
      default: true
    },
    // TODO :  v-model   
    value: {
      type: [Number, String],
      default: null
    },
    counter: {
      type: Number
    },
    maxlength: {
      type: Number
    },
    icon: {
      type: String
    },
    prependIcon: {
      type: String
    },
    //  
    customClass: {
      type: String
    },
    hideDetail: {
      type: Boolean,
      default: false
    },
    hint: {
      type: [String, Number]
    },
    backgroundColor: {
      type: String,
      default: null
    },
    color: {
      type: String,
      default: null
    },
    outline: {
      type: Boolean,
      default: false
    },
    solo: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      vValue: null
    };
  },
  computed: {
    //     
    error () {
      return this.errorMsg && this.errorMsg.length > 0;
    }
  },
  watch: {
    // todo :  v-model  
    value () {
      this.vValue = this.value;
    }
  },
  /* Vue lifecycle: created, mounted, destroyed, etc */
  /* methods */
  methods: {
    input () {
      // TODO :   
      this.$emit('input', this.vValue);
    }
  }
};
</script>

<style>
::-webkit-input-placeholder {
   font-style: italic;
   font-size: 1rem;
}
:-moz-placeholder {
   font-style: italic;
   font-size: 1rem;  
}
::-moz-placeholder {
   font-style: italic;
   font-size: 1rem;  
}
:-ms-input-placeholder {  
   font-style: italic;
   font-size: 1rem; 
}
</style>




