<!--
   :  
  Detail :
  *
  examples:
  *
  -->
<template>
  <div id="">
    <v-container grid-list-xl fluid>
      <v-layout row wrap>
        <!--  grid-->
        <v-flex xs12>
          <y-data-table 
            title=""
            ref="dataTable"
            :headers="counselGridHeaderOptions"
            :items="gridData"
            :editable="editable"
            :excel-down="true"
            :print="true">
          </y-data-table>
        </v-flex>
        <!-- -->
        <v-flex xs12 class="text-xs-right">
          <y-btn
            v-if="editable"
            type="clear"
            title="Excel"
          />
          <y-btn
            v-if="editable"
            type="clear"
            title="Print"
          />
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
export default {
  /* attributes: name, components, props, data */
  name: 'y-healthCounsel',
  props: {
    gridData: [],
    gridHeaderOptions: [],
  },
  data: () => ({
    editable: true,
    isSubmit: false,
  }),
  //* Vue lifecycle: created, mounted, destroyed, etc */
  beforeCreate () {
  },
  created () {
  },
  beforeMount () {
    Object.assign(this.$data, this.$options.data());
    this.init();
    this.getList();
  },
  mounted () {
  },
  beforeDestory () {
  },
  //* methods */
  methods: {
    init () {
      //   
      this.counselGridHeaderOptions = [
        { text: '', name: 'delete', width: '40%', type: 'delete' },
        { text: '', name: 'visit_dt', width: '40%' },
        { text: '', name: 'history', width: '40%', align: 'center' },
        { text: '', name: 'current', width: '30%', align: 'center' },
        { text: '', name: 'symptom', width: '15%', align: 'right' },
        { text: '', name: 'content', width: '15%', align: 'center' }
      ];
    },
    getList () {
      // var baseUrl = this.$format(selectConfig.menus.get.url, this.menuId);
      // var url = this.$backend.getUrl(baseUrl);
      // var self = this;
      setTimeout(() => {
        this.gridData = [
          { visit_dt: '2018-12-19', history: '', current: '', symptom: '', content: '' },
        ];
      }, 2000);
    },
  }
};
</script>


