<!--
   : List   / Sample 
  Detail :
  *
  examples:
  *
  -->
<template>
<div id="">
    <v-container grid-list-xl fluid>
      <v-layout row wrap>
        <v-flex xs12>
          <v-widget title="Control " content-bg="white">
            <v-layout row wrap slot="widget-content">
              <!-- TODO:    -->
              <!--  Text   -->
              <v-flex xs12 sm6>
                <y-text
                  :editable="editable"
                  :counter="200"
                  :maxlength="200"
                  label=""
                  name="type"
                  v-model="demo.type"
                  prepend-icon="edit"
                >
                </y-text>
              </v-flex>
              <!--  Text   -->
              <v-flex xs12 sm6>
                <y-text
                  :editable="editable"
                  :counter="50"
                  :max-length="100"
                  label="*"
                  name="code"
                  v-model="demo.code"
                  v-validate="'required'"
                  data-vv-name="workTitle"
                  :error-msg="errors.first('workTitle')"
                >
                </y-text>
              </v-flex>
              <v-flex xs12 sm6>
                <y-select
                  :editable="editable"
                  label=""
                  name="deptPk"
                  v-model="demo.deptPk"
                  item-text="value"
                  item-value="key"
                  :combo-items="comboItems"
                >
                </y-select>
              </v-flex>
              <v-flex xs12 sm6>
                <v-switch
                  label=""
                  v-model="demo.usable"
                ></v-switch>
              </v-flex>
              <v-flex xs12 sm6>
                <y-datepicker 
                  :editable="editable"
                  label=""
                  name="date"
                  v-model="demo.date"
                  default-type="today"
                  v-validate="'required'"
                  data-vv-name="date"
                  :error-msg="errors.first('date')"
                >
                </y-datepicker>
              </v-flex>
              <!--     -->
              <v-flex xs12 class="text-xs-right">
                <y-btn
                  v-if="editable"
                  :action-url="saveUrl"
                  :param="demo"
                  :is-submit="isSubmit"
                  type="save"
                  title=""
                  action-type="POST"
                  beforeSubmit = "beforeSubmit"
                  @beforeSubmit="beforeSubmit"
                  @btnClicked="btnSaveClickedCallback" 
                  @btnClickedErrorCallback="btnClickedErrorCallback"
                />
                <y-btn
                  v-if="editable"
                  :action-url="deleteUrl"
                  :param="demo"
                  :is-submit="isSubmit"
                  type="delete"
                  title=""
                  action-type="POST"
                  beforeSubmit = "getConfirm"
                  @getConfirm="getConfirm"
                  @btnClicked="btnDeleteClickedCallback" 
                  @btnClickedErrorCallback="btnClickedErrorCallback"
                />
                <y-btn
                  v-if="editable"
                  type="clear"
                  title=""
                  @btnClicked="btnClearClickedCallback" 
                />
              </v-flex>
              <v-flex xs12>
                
              </v-flex>
              <!-- /TODO:    -->
            </v-layout>
          </v-widget>
              <!-- Grid   -->
              <v-widget title="Grid " content-bg="white">
                <v-layout row wrap slot="widget-content">
                  <!-- TODO:    -->
                  <v-flex xs12>
                    <y-data-table 
                      title=""
                      ref="dataTable"
                      :headers="gridHeaderOptions"
                      :items="gridData"
                      :editable="editable"
                      :excel-down="true"
                      :print="true">
                    </y-data-table>
                  </v-flex>
                <!-- TODO:    -->
                </v-layout>
              </v-widget>
        </v-flex>
      </v-layout>
    </v-container>
</div>
</template>

<script>
import VWidget from '@/components/VWidget'; // TODO :  .
export default {
  /* attributes: name, components, props, data */
  name: 'y-sample1',
  components: {
    VWidget
  },
  props: {
  },
  data: () => ({
    demo: {
      type: '',
      code: '',
      deptPk: null,
      comboItems: null,
      usable: true,
      date: null
    },
    editable: true,
    isSubmit: false,
    gridData: [],
    gridHeaderOptions: [],
    saveUrl: '',
    deleteUrl: ''
  }),
  //* Vue lifecycle: created, mounted, destroyed, etc */
  beforeCreate () {
  },
  created () {
  },
  beforeMount () {
    Object.assign(this.$data, this.$options.data());
    this.init();
    this.getList();
  },
  mounted () {
  },
  beforeDestory () {
  },
  //* methods */
  methods: {
    init () {
      //    
      this.comboItems = [
        { key: '', value: '' },
        { key: 'key1', value: '' },
        { key: 'key2', value: '' },
        { key: 'key3', value: 'SDG' },
        { key: 'key3', value: '' },
      ];

      //   
      this.gridHeaderOptions = [
        { text: '', name: 'delete', width: '40%', type: 'delete' },
        { text: '', name: 'type', width: '40%' },
        { text: '', name: 'abbr', width: '30%', align: 'center' },
        { text: '', name: 'order', width: '15%', align: 'right' },
        { text: '', name: 'usable', width: '15%', align: 'center' }
      ];
    },
    /**   UI   **/
    beforeSubmit () {
      this.checkValidation();
    },
    /**
     *   
     */
    checkValidation () {
      this.$validator.validateAll().then((_result) => {
        this.isSubmit = _result;
        // TODO :    
        //  ./event.js   
        if (!this.isSubmit) window.getApp.$emit('APP_VALID_ERROR', '   .');
      }).catch(() => {
        this.isSubmit = false;
      });
    },
    getList () {
      // var baseUrl = this.$format(selectConfig.menus.get.url, this.menuId);
      // var url = this.$backend.getUrl(baseUrl);
      // var self = this;
      setTimeout(() => {
        this.gridData = [
          { type: '', abbr: '', order: '1', usable: '' },
          { type: '', abbr: '', order: '2', usable: '' },
          { type: '', abbr: '', order: '3', usable: '' },
          { type: '', abbr: '', order: '4', usable: '' },
        ];
      }, 2000);
    },
    /**
     *   .
     */
    getConfirm () {
    },
    /** button   **/
    btnClearClickedCallback () {
      this.$validator.reset();
      this.$emit('APP_REQUEST_SUCCESS', '   .');
    },
    btnSaveClickedCallback (_result) {
      this.$emit('APP_REQUEST_SUCCESS', '   .');
    },
    btnDeleteClickedCallback (_result) {
      this.$emit('APP_REQUEST_SUCCESS', '   .');
    },
    btnClickedErrorCallback (_result) {
      this.$emit('APP_REQUEST_ERROR', _result);
    },
    /** end button   **/
  }
};
</script>


