<!--
   :   - 
  Detail :  /
  *
  examples:
  *
  -->
<template>
<div id="examinationType">
  <v-container grid-list-xl fluid>
    <v-layout row wrap>
      <v-flex xs12>
        <v-widget content-bg="white">
          <v-layout row wrap slot="widget-content">
            <v-flex xs12 sm6>
              <y-text
              :editable="editable"
              :counter="30"
              :maxlength="30"
              label=""
              name="type"
              v-model="healthType.type"
              v-validate="'required'"
              data-vv-name="type"
              :error-msg="errors.first('type')"
              prepend-icon="edit"
              >
              </y-text>
            </v-flex>
            <v-flex xs12 sm6>
                <y-text
                :editable="editable"
                :counter="30"
                :maxlength="30"
                label=""
                name="code"
                v-model="healthType.code"
                v-validate="'required'"
                data-vv-name="code"
                :error-msg="errors.first('code')"
                prepend-icon="edit"
                >
                </y-text>
            </v-flex>
            <v-flex xs12 sm6>
                <y-text
                :editable="editable"
                :counter="30"
                :maxlength="30"
                label=""
                name="abbr"
                v-model="healthType.abbr"
                prepend-icon="edit"
                >
                </y-text>
            </v-flex>
            <v-flex xs12 sm6>
                <y-text
                :editable="editable"
                :counter="30"
                :maxlength="30"
                label=""
                name="order"
                v-model="healthType.order"
                prepend-icon="edit"
                >
                </y-text>
            </v-flex>
            <v-flex xs12 sm4>
                <v-switch
                label=""
                v-model="healthType.usable"
                ></v-switch>
            </v-flex>
            <v-flex xs12 sm4>
                <v-switch
                label=""
                v-model="healthType.planable"
                ></v-switch>
            </v-flex>
            <v-flex xs12 class="text-xs-right">
                <y-btn
                v-if="editable"
                :action-url="saveUrl"
                :param="demo"
                :is-submit="isSubmit"
                type="save"
                title=""
                action-type="POST"
                beforeSubmit = "beforeSubmit"
                @beforeSubmit="beforeSubmit"
                @btnClicked="btnSaveClickedCallback" 
                @btnClickedErrorCallback="btnClickedErrorCallback"
                />
            </v-flex>
          </v-layout>
        </v-widget>
        <v-widget content-bg="white">
          <v-layout row wrap slot="widget-content">
            <v-flex xs12>
              <y-data-table 
              title=""
              ref="dataTable"
              :headers="gridHeaderOptions"
              :items="gridData"
              :editable="editable"
              :excel-down="true"
              :print="true">
              </y-data-table>
            </v-flex>
          </v-layout>
        </v-widget>
      </v-flex>
    </v-layout>
  </v-container>
</div>
</template>

<script>
import VWidget from '@/components/VWidget';
export default {
  /* attributes: name, components, props, data */
  name: 'y-ExaminationType',
  components: {
    VWidget
  },
  props: {
  },
  data: () => ({
    healthType: {
      type: '',
      code: '',
      abbr: '',
      order: '',
      usable: true,
      planable: true,
    },
    editable: true,
    isSubmit: false,
    gridData: [],
    gridHeaderOptions: [],
    saveUrl: '',
  }),
  //* Vue lifecycle: created, mounted, destroyed, etc */
  beforeCreate () {
  },
  created () {
  },
  beforeMount () {
    Object.assign(this.$data, this.$options.data());
    this.init();
    this.getList();
  },
  mounted () {
  },
  beforeDestory () {
  },
  //* methods */
  methods: {
    init () {
      //   
      this.gridHeaderOptions = [
        { text: '', name: 'delete', width: '10%', type: 'delete' },
        { text: '', name: 'type', width: '40%' },
        { text: '', name: 'abbr', width: '30%', align: 'center' },
        { text: '', name: 'order', width: '15%', align: 'right' },
        { text: '', name: 'usable', width: '15%', align: 'center' }
      ];
    },
    /**   UI   **/
    beforeSubmit () {
      this.checkValidation();
    },
    /**
     *   
     */
    checkValidation () {
      this.$validator.validateAll().then((_result) => {
        this.isSubmit = _result;
        // TODO :    
        //  ./event.js   
        if (!this.isSubmit) window.getApp.$emit('APP_VALID_ERROR', '   .');
      }).catch(() => {
        this.isSubmit = false;
      });
    },
    getList () {
      // var baseUrl = this.$format(selectConfig.menus.get.url, this.menuId);
      // var url = this.$backend.getUrl(baseUrl);
      // var self = this;
      setTimeout(() => {
        this.gridData = [
          { type: '', abbr: '', order: '1', usable: '' },
          { type: '', abbr: '', order: '2', usable: '' },
          { type: '', abbr: '', order: '3', usable: '' },
          { type: '', abbr: '', order: '4', usable: '' },
        ];
      }, 2000);
    },
    /**
     *   .
     */
    getConfirm () {
    },
    /** button   **/
    btnSaveClickedCallback (_result) {
      this.$emit('APP_REQUEST_SUCCESS', '   .');
    },
    btnClickedErrorCallback (_result) {
      this.$emit('APP_REQUEST_ERROR', _result);
    },
    /** end button   **/
  }
};
</script>


