<!--
   :  >  
  Detail :
  *
  examples:
  *
  -->
<template>
  <div id>
    <v-container grid-list-xl fluid>
      <!--  -->
      <v-widget title="" content-bg="white">
        <v-layout row wrap slot="widget-content">
          <v-flex xs12 sm6>
            <y-datepicker
              :editable="editable"
              label=""
              name="date1"
              v-model="demo.date1"
              default-type="today"
              v-validate="'required'"
              data-vv-name="date1"
              :error-msg="errors.first('date1')"
            ></y-datepicker>
          </v-flex>
          <v-flex xs12 sm6>
            <y-datepicker
              :editable="editable"
              label=""
              name="date2"
              v-model="demo.date2"
              default-type="today"
              v-validate="'required'"
              data-vv-name="date2"
              :error-msg="errors.first('date2')"
            ></y-datepicker>
          </v-flex>
        </v-layout>
      </v-widget>
      <!--  -->
      <v-widget title=" " content-bg="white">
        <v-layout row wrap slot="widget-content">
          <!--     -->
          <v-flex xs12 class="text-xs-right">
            <y-btn title="" type="search"/>
            <y-btn title="Excel"/>
            <y-btn title="Print"/>
          </v-flex>

          <!-- TODO:    -->
          <v-flex xs12>
            <y-data-table
              title=""
              ref="dataTable"
              :headers="gridHeaderOptions"
              :items="gridData"
              :editable="editable"
              :excel-down="true"
              :print="true"
            ></y-data-table>
          </v-flex>
          <!-- TODO:    -->
        </v-layout>
      </v-widget>
      <!--  -->
      <v-widget title="" content-bg="white">
        <v-layout row wrap slot="widget-content">
          <!--   -->
        </v-layout>
      </v-widget>
    </v-container>
  </div>
</template>

<script>
import VWidget from "@/components/VWidget";
export default {
  /* attributes: name, components, props, data */
  name: "y-checkReserve",
  components: {
    VWidget
  },
  props: {},
  data: () => ({
    demo: {
      date1: null,
      date2: null
    },
    editable: true,
    gridData: [],

  }),
  //* Vue lifecycle: created, mounted, destroyed, etc */
  beforeCreate() {},
  created() {},
  beforeMount() {
    Object.assign(this.$data, this.$options.data());
    this.init();
    this.getList();
  },
  mounted() {},
  beforeDestory() {},
  //* methods */
  methods: {
    init () {
      this.gridHeaderOptions = [
        { text: "", name: "c1", width: "20%", align: "left" },
        { text: "", name: "c2", width: "25%", align: "left" },
        { text: "", name: "c3", width: "25%", align: "center" },
        { text: "", name: "c4", width: "15%", align: "center" },
        { text: "", name: "c5", width: "15%", align: "center" }
      ];
    },
    getList() {
      // var baseUrl = this.$format(selectConfig.menus.get.url, this.menuId);
      // var url = this.$backend.getUrl(baseUrl);
      // var self = this;
      setTimeout(() => {
        this.gridData = [
          {
            c1: "",
            c2: "2015 ",
            c3: "2015-03-01 ~ 2015-04-30",
            c4: "2015-03-31",
            c5: ""
          },
          {
            c1: "",
            c2: "2015 ",
            c3: "2015-04-01 ~ 2015-06-23",
            c4: "2015-03-31",
            c5: "120"
          },
          {
            c1: "",
            c2: "2015 ",
            c3: "2015-03-01 ~ 2015-06-30",
            c4: "2015-04-30",
            c5: "30"
          }
        ];
      }, 2000);
    },
    /** button   **/
    btnClearClickedCallback() {
      this.$validator.reset();
      this.$emit("APP_REQUEST_SUCCESS", "   .");
    },
    btnSaveClickedCallback(_result) {
      this.$emit("APP_REQUEST_SUCCESS", "   .");
    },
    btnDeleteClickedCallback(_result) {
      this.$emit("APP_REQUEST_SUCCESS", "   .");
    },
    btnClickedErrorCallback (_result) {
      this.$emit("APP_REQUEST_ERROR", _result);
    }
    /** end button   **/
  }
};
</script>


