<!--
   :   - 
  Detail :  /
  *
  examples:
  *
  -->
<template>
<div id="examinationItem">
  <v-container grid-list-xl fluid>
    <v-layout row wrap>
      <v-flex xs12>
        <v-widget content-bg="white">
          <v-layout row wrap slot="widget-content">
            <v-flex xs12 sm6>
              <y-select
                :editable="editable"
                label=""
                name="exam"
                v-model="healthItem.exam"
                item-text="value"
                item-value="key"
                :combo-items="comboItems"
              >
              </y-select>
            </v-flex>
            <v-flex xs12 sm6>
              <y-text
              :editable="editable"
              :counter="30"
              :maxlength="30"
              label=""
              name="name"
              v-model="healthItem.name"
              v-validate="'required'"
              data-vv-name="name"
              :error-msg="errors.first('name')"
              prepend-icon="edit"
              >
              </y-text>
            </v-flex>
            <v-flex xs12 sm6>
                <y-text
                :editable="editable"
                :counter="30"
                :maxlength="30"
                label=""
                name="unit"
                v-model="healthItem.unit"
                prepend-icon="edit"
                >
                </y-text>
            </v-flex>
            <v-flex xs12 sm6>
              <y-select
                :editable="editable"
                label=""
                name="resultType"
                v-model="healthItem.resultType"
                item-text="value"
                item-value="key"
                :combo-items="comboItems"
              >
              </y-select>
            </v-flex>
            <v-flex xs12>
                <v-textarea
                  :editable="editable"
                  :counter="300"
                  :maxlength="300"
                  label=""
                  name="reference"
                  v-model="healthItem.reference"
                  prepend-icon="edit"
                ></v-textarea>
            </v-flex>
            <v-flex xs12>
                <v-textarea
                  :editable="editable"
                  :counter="300"
                  :maxlength="300"
                  label=""
                  name="itemExplan"
                  v-model="healthItem.itemExplan"
                  prepend-icon="edit"
                ></v-textarea>
            </v-flex>
            <v-flex xs12 sm6>
                <y-text
                :editable="editable"
                :counter="30"
                :maxlength="30"
                label=""
                name="order"
                v-model="healthItem.order"
                prepend-icon="edit"
                >
                </y-text>
            </v-flex>
            <v-flex xs12 sm6>
                <v-switch
                label=""
                v-model="healthItem.usable"
                ></v-switch>
            </v-flex>
            <v-flex xs12 class="text-xs-right">
                <y-btn
                v-if="editable"
                :action-url="saveUrl"
                :param="demo"
                :is-submit="isSubmit"
                type="save"
                title=""
                action-type="POST"
                beforeSubmit = "beforeSubmit"
                @beforeSubmit="beforeSubmit"
                @btnClicked="btnSaveClickedCallback" 
                @btnClickedErrorCallback="btnClickedErrorCallback"
                />
            </v-flex>
          </v-layout>
        </v-widget>
        <v-widget content-bg="white">
          <v-layout row wrap slot="widget-content">
            <v-flex xs12>
              <y-data-table 
              title=" "
              ref="dataTable"
              :headers="gridHeaderOptions"
              :items="gridData"
              :editable="editable"
              :excel-down="true"
              :print="true">
              </y-data-table>
          </v-flex>
          </v-layout>
        </v-widget>
      </v-flex>
    </v-layout>
  </v-container>
</div>
</template>

<script>
import VWidget from '@/components/VWidget';
export default {
  /* attributes: name, components, props, data */
  name: 'y-ExaminationItem',
  components: {
    VWidget
  },
  props: {
  },
  data: () => ({
    healthItem: {
      itemName: '',
      unit: '',
      reference: '',
      itemExplan: '',
      order: '',
      usable: true,
      exam: null,
      resultType: null
    },
    editable: true,
    isSubmit: false,
    gridData: [],
    gridHeaderOptions: [],
    saveUrl: '',
  }),
  //* Vue lifecycle: created, mounted, destroyed, etc */
  beforeCreate () {
  },
  created () {
  },
  beforeMount () {
    Object.assign(this.$data, this.$options.data());
    this.init();
    this.getList();
  },
  mounted () {
  },
  beforeDestory () {
  },
  //* methods */
  methods: {
    init () {
      //   
      this.gridHeaderOptions = [
        { text: '', name: 'delete', width: '10%', type: 'delete' },
        { text: '', name: 'name', width: '40%', align: 'center' },
        { text: '', name: 'remark', width: '30%', align: 'center' },
        { text: '', name: 'order', width: '15%', align: 'center' },
        { text: '', name: 'usable', width: '15%', align: 'center' },
        { text: '', name: 'usable', width: '15%', align: 'center' },
        { text: '', name: 'usable', width: '15%', align: 'center' }
      ];
    },
    /**   UI   **/
    beforeSubmit () {
      this.checkValidation();
    },
    /**
     *   
     */
    checkValidation () {
      this.$validator.validateAll().then((_result) => {
        this.isSubmit = _result;
        // TODO :    
        //  ./event.js   
        if (!this.isSubmit) window.getApp.$emit('APP_VALID_ERROR', '   .');
      }).catch(() => {
        this.isSubmit = false;
      });
    },
    getList () {
      // var baseUrl = this.$format(selectConfig.menus.get.url, this.menuId);
      // var url = this.$backend.getUrl(baseUrl);
      // var self = this;
      setTimeout(() => {
        this.gridData = [
          { name: '  ', remark: ', ', order: '1', usable: '' },
          { name: '', remark: ' ', order: '2', usable: '' },
          { name: ' ', remark: ', , , , , ', order: '3', usable: '' },
          { name: '', remark: ', , ,  ', order: '4', usable: '' },
        ];
      }, 2000);
    },
    /**
     *   .
     */
    getConfirm () {
    },
    /** button   **/
    btnSaveClickedCallback (_result) {
      this.$emit('APP_REQUEST_SUCCESS', '   .');
    },
    btnClickedErrorCallback (_result) {
      this.$emit('APP_REQUEST_ERROR', _result);
    },
    /** end button   **/
  }
};
</script>


