<!--
   :  - 
  Detail :
  *
  examples:
  *
  -->
<template>
  <div id="">
    <v-container grid-list-xl fluid>
      <v-layout row wrap>
        <v-flex xs12>
          <v-widget title=" " content-bg="white">
            <v-layout row wrap slot="widget-content">
              <!-- -->
              <v-flex xs12 sm3>
                <y-datepicker 
                  mg-400
                  :editable="editable"
                  label=""
                  name="date"
                  v-model="demo.storeDt"
                  default-type="today"
                  v-validate="'required'"
                  data-vv-name="date"
                  :error-msg="errors.first('date')"
                >
                </y-datepicker>
              </v-flex>
              <!--  grid-->
              <v-flex xs12>
                <y-data-table 
                  title=""
                  ref="dataTable"
                  :headers="gridHeaderOptions"
                  :items="gridData"
                  :editable="editable"
                  :excel-down="true"
                  :print="true">
                </y-data-table>
              </v-flex>
              <!-- -->
              <v-flex xs12 class="text-xs-right">
                <y-btn
                  v-if="editable"
                  :action-url="saveUrl"
                  :param="demo"
                  :is-submit="isSubmit"
                  type="save"
                  title=""
                  action-type="POST"
                  beforeSubmit = "beforeSubmit"
                  @beforeSubmit="beforeSubmit"
                  @btnClicked="btnSaveClickedCallback" 
                  @btnClickedErrorCallback="btnClickedErrorCallback"
                />
              </v-flex>
              <!-- -->
              <v-flex xs12 sm3>
                <y-datepicker 
                  mg-400
                  :editable="editable"
                  label=" "
                  name="date"
                  v-model="demo.storeStartDt"
                  default-type="today"
                  v-validate="'required'"
                  data-vv-name="date"
                  :error-msg="errors.first('date')"
                >
                </y-datepicker>
              </v-flex>
              <v-flex xs12 sm3>
                <y-datepicker 
                  mg-400
                  :editable="editable"
                  label=" "
                  name="date"
                  v-model="demo.storeEndDt"
                  default-type="today"
                  v-validate="'required'"
                  data-vv-name="date"
                  :error-msg="errors.first('date')"
                >
                </y-datepicker>
              </v-flex>
              <v-flex xs12 sm6>
                <y-select
                  :editable="editable"
                  label=""
                  name="medicinePk"
                  v-model="demo.medicinePk"
                  item-text="value"
                  item-value="key"
                  :combo-items="comboItems"
                >
                </y-select>
              </v-flex>
              <!-- -->
              <v-flex xs12 class="text-xs-right">
                <y-btn
                  v-if="editable"
                  type="clear"
                  title="Excel"
                />
                <y-btn
                  v-if="editable"
                  type="clear"
                  title="Print"
                />
              </v-flex>
              <!--  grid-->
              <v-flex xs12>
                <y-data-table 
                  title=" "
                  ref="dataTable"
                  :headers="listGridHeaderOptions"
                  :items="listGridData"
                  :editable="editable"
                  :excel-down="true"
                  :print="true">
                </y-data-table>
              </v-flex>
            </v-layout>
          </v-widget>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import VWidget from '@/components/VWidget';
export default {
  /* attributes: name, components, props, data */
  name: 'y-store',
  components: {
    VWidget
  },
  props: {
  },
  data: () => ({
    demo: {
      medicinePk: null,
      comboItems: null,
      storeDt: null,
      storeStartDt: null,
      storeEndDt: null
    },
    editable: true,
    isSubmit: false,
    gridData: [],
    gridHeaderOptions: [],
    saveUrl: '',
    deleteUrl: ''
  }),
  //* Vue lifecycle: created, mounted, destroyed, etc */
  beforeCreate () {
  },
  created () {
  },
  beforeMount () {
    Object.assign(this.$data, this.$options.data());
    this.init();
    this.getList();
  },
  mounted () {
  },
  beforeDestory () {
  },
  //* methods */
  methods: {
    init () {
      //    
      this.comboItems = [
        { key: '', value: '' },
        { key: 'key1', value: '' },
        { key: 'key2', value: '' },
        { key: 'key3', value: '' },
      ];

      //   
      this.gridHeaderOptions = [
        { text: '', name: 'medicine_nm', width: '40%' },
        { text: '', name: 'unit', width: '30%', align: 'center' },
        { text: '', name: 'stock', width: '15%', type: 'text' },
        { text: '', name: 'rmk', width: '15%', type: 'text' }
      ];

      //    
      this.listGridHeaderOptions = [
        { text: '', name: 'delete', width: '40%', type: 'delete' },
        { text: '', name: 'store_dt', width: '40%' },
        { text: '', name: 'medicine_nm', width: '40%', align: 'center' },
        { text: '', name: 'unit', width: '30%', align: 'center' },
        { text: '', name: 'stock', width: '15%', align: 'right' },
        { text: '', name: 'rmk', width: '15%', align: 'center' }
      ];
    },
    /**   UI   **/
    beforeSubmit () {
      this.checkValidation();
    },
    /**
     *   
     */
    checkValidation () {
      this.$validator.validateAll().then((_result) => {
        this.isSubmit = _result;
        // TODO :    
        //  ./event.js   
        if (!this.isSubmit) window.getApp.$emit('APP_VALID_ERROR', '   .');
      }).catch(() => {
        this.isSubmit = false;
      });
    },
    getList () {
      // var baseUrl = this.$format(selectConfig.menus.get.url, this.menuId);
      // var url = this.$backend.getUrl(baseUrl);
      // var self = this;
      setTimeout(() => {
        this.gridData = [
          { medicine_nm: '', unit: 'ml', stock: '100', rmk: '' },
          { medicine_nm: '', unit: '', stock: '', rmk: '' },
          { medicine_nm: '', unit: '', stock: '', rmk: '' },
          { medicine_nm: '', unit: '', stock: '', rmk: '' },
        ];
        this.listGridData = [
          { store_dt: '2018-12-19', medicine_nm: '', unit: 'ml', stock: '100', rmk: '' },
        ];
      }, 2000);
    },
    /** button   **/
    btnClearClickedCallback () {
      this.$validator.reset();
      this.$emit('APP_REQUEST_SUCCESS', '   .');
    },
    btnSaveClickedCallback (_result) {
      this.$emit('APP_REQUEST_SUCCESS', '   .');
    },
    btnDeleteClickedCallback (_result) {
      this.$emit('APP_REQUEST_SUCCESS', '   .');
    },
    btnClickedErrorCallback (_result) {
      this.$emit('APP_REQUEST_ERROR', _result);
    },
    /** end button   **/
  }
};
</script>


