<!--
 : select  select 
Detail :
  comboConfig.js     select    option 
 * 
examples: 
 *  
-->
<template>
  <div>
    <v-select
      v-if="editable"
      ref="select"
      :color="color"
      :label="label"
      :name="name"
      :items="items"
      :item-text="itemText"
      :item-value="itemValue"
      v-model="vValue"
      @input="input"
      :hide-selected="hideSelected"
      :error="error"
      :error-messages="errorMsg"
    ></v-select>
    <v-text-field
      v-else
      v-model="text"
      :placeholder="text ? null : $t('message.noData')"
      :label="label"
      readonly
      ></v-text-field>
  </div>
</template>

<script>
import comboConfig from '@/js/comboConfig';
export default {
  /* attributes: name, components, props, data */
  name: 'y-select',
  $_veeValidate: {
    name () {
      return this.name;
    },
    value () {
      return this.vValue;
    }
  },
  props: {
    // selectConfig.js  select  
    itemSearchKey: {
      type: String,
      default: ''
    },
    name: {
      type: String
    },
    label: {  //     
      type: String,
      default: ''
    },
    // TODO :     
    errorMsg: {
      type: String,
      required: false
    },
    //   
    editable: {
      type: Boolean,
      default: true
    },
    // TODO :  v-model   
    value: {
      type: [Number, String],
      default: null
    },
    //        
    hideSelected: {
      type: Boolean,
      default: false
    },
    //      
    isClearText: {
      type: Boolean,
      default: false
    },
    color: {
      type: String
    },
    type: { // select : edit, search   , ( ''  ' )
      type: String,
      default: 'edit'
    },
    itemText: {
      type: String,
      default: 'edit'
    },
    itemValue: {
      type: String,
      default: 'edit'
    },
    //     
    comboItems: {
      type: Array,
      default: []
    }
  },
  data () {
    return {
      items: [],
      item: {},
      itemSubValue: null,
      vValue: null,
      text: null,
      orgItems: []
    };
  },
  computed: {
    //     
    error () {
      return this.errorMsg && this.errorMsg.length > 0;
    }
  },
  watch: {
    items () {
      this.vValue = this.value;
    },
    // todo :  v-model  ( , db        )
    value () {
      this.vValue = this.value;
    }
  },
  /* Vue lifecycle: created, mounted, destroyed, etc */
  beforeMount () {
    this.getSelectItems();
  },
  /* methods */
  methods: {
    getSelectItems () {
      if (!this.itemSearchKey) {
        this.items = this.comboItems;
        console.log(JSON.stringify(this.items));
        return;
      }
      
      // this.item = comboConfig[this.itemSearchKey];
      // this.$ajax.url = this.item.url;
      // this.$ajax.param = null;
      // let self = this;
      // this.$ajax.requestGet((_result) => {
      //   self.items = typeof _result.content !== 'undefined' ? _result.content : _result
      //   if (self.items.length > 0) {
      //     var item = {}
      //     item[this.item.itemName] = this.type === 'search' ? this.$t('message.all') :  this.$t('message.select')
      //     item[this.item.itemKey] = null
      //     self.items.unshift(item)
      //   }
      //   if (!this.editable) this.getSelectedText();
      // }, (_error) => {
      //   console.log('_error' + JSON.stringify(_error));
      // })
    },
    input () {
      // TODO :   
      this.$emit('input', this.vValue);
      this.$refs.select.$refs.input.blur();
      window.getApp.$emit('APP_KEYBOARD_HIDE');
      //    
      if (this.isClearText) {
        this.$nextTick(() => {
          this.vValue = null;
        });
      }
    },
    getSelectItem (_pk) {
      var pk = _pk;
      pk = _pk ? _pk : this.vValue;
      if (!pk) return null;
      var filter = this.items.filter((_item) => {
        return _item[this.item.itemKey] === pk;
      });

      if (filter.length <= 0) return null;
      var value = {
        item: filter[0],
        itemInfo: {
          key: this.itemText,
          value: this.itemValue
        }
      };
      return value;
    },
    getItems () {
      return this.items;
    },
    getSelectedText () {
      var filter = null;
      if (!this.value) this.text = null;
      else {
        filter = this.items.filter((_item) => {
          return _item[this.item.itemKey] === this.value;
        });
        if (filter.length > 0) this.text = filter[0][this.item.itemName];
        else this.text = null;
      }
    }
  }
};
</script>

<style>
::-webkit-input-placeholder {
   font-style: italic;
   font-size: 1rem;
}
:-moz-placeholder {
   font-style: italic;
   font-size: 1rem;  
}
::-moz-placeholder {
   font-style: italic;
   font-size: 1rem;  
}
:-ms-input-placeholder {  
   font-style: italic;
   font-size: 1rem; 
}
</style>



