<!--
 : v-date-picker datepicker 
Detail :
 *  :
  - vee-validate   
  -      
  - ($validator.validateAll   )   
  - ,   2        
examples: 
 *  <YDatepicker 
      label="*"
      name="rqstDt"
      v-model="saveData.workOrder.rqstDt"
      defaultType="today"
      validate-type="required"
      :parentValidateCheck="validateCheck"
      >
    </YDatepicker>
-->
<template>
  <div>
    <v-menu
      v-if="editable"
      ref="datePickerMenu"
      :close-on-content-click="false"
      v-model="datePickerMenu"
      :nudge-right="40"
      :return-value.sync="vValue"
      lazy
      transition="scale-transition"
      offset-y
      full-width
      min-width="100px"
      class="mb-0"
      >
      <v-text-field
        slot="activator"
        ref="date"
        v-model="localFormattedDate"
        :label="label"
        prepend-icon="event"
        readonly
        clearable
        @click:clear="clearValue"
        :error="error"
        :error-messages="errorMsg"
        >
      </v-text-field>
      <v-date-picker
        v-model="vValue"
        @input="input"
        :name="name"
        :locale="locale"
        no-title
        scrollable
        autosave>
      </v-date-picker>
    </v-menu>
    <v-text-field
      v-else
      v-model="vValue"
      :placeholder="vValue ? null : '  .'"
      :label="label"
      readonly
      ></v-text-field>
  </div>
</template>

<script>
let localeMapper = require('@/locale/localeMapper.json');
// require("moment/min/locales.min");
export default {
  /* attributes: name, components, props, data */
  name: 'y-datepicker',
  // TODO : veeValidate error     
  $_veeValidate: {
    name () {
      return this.name;
    },
    value () {
      return this.vValue;
    }
  },
  props: {
    label: {
      type: String,
      default: ' .'
    },
    name: {
      type: String,
      default: 'date'
    },
    defaultType: {
      type: String,
      default: ''
    },
    childValidateType: {
      type: [String, Object],
      default: null
    },
    // TODO :     
    errorMsg: {
      type: String,
      required: false,
    },
    //   
    editable: {
      type: Boolean,
      default: true
    },
    // TODO :  v-model   
    value: {
      type: [String, Date],
      default: null
    }
  },
  data () {
    return {
      datePickerMenu: null,
      vValue: null, // TODO : datepicker  , YYYY-MM-DD(ISO )  
      localFormattedDate: null, // TODO :    v-text-field   local date format  (    )
      locale: 'ko-KR' // window.getApp.locale.datepicker
    };
  },
  computed: {
    //     
    error () {
      return this.errorMsg && this.errorMsg.length > 0;
    }
  },
  watch: {
    errorMsg () {
      console.log(':::::::::: debug:' + this.errorMsg);
    },
    vValue () {
      this.setLocalFormattedDate();
    },
    // todo :  v-model  
    value () {
      this.vValue = this.value;
    }
  },
  /* Vue lifecycle: created, mounted, destroyed, etc */
  beforeMount () {
    window.getApp.$on('LOCALE_CHANGE', (_localeCode) => {
      // datepicker 
      this.locale = localeMapper[_localeCode].datepicker;
      this.setLocalFormattedDate();
    });
  },
  mounted () {
    if (!this.defaultType) return;
    if (this.defaultType === 'today') {
      if (this.value) this.vValue = this.value;
      else this.vValue = this.$comm.getToday();
    } else {
      this.vValue = this.$comm.getPrevDate(this.defaultType);
    }
  },
  beforeDestroy () {
    // TODO : remove event listener,      
    window.getApp.$off('LOCALE_CHANGE');
  },
  /* methods */
  methods: {
    input () {
      this.$refs.datePickerMenu.save(this.vValue);
      this.$emit('input', this.vValue);
    },
    /**
     * delete icon   
     */
    clearValue () {
      this.vValue = null;
      this.localFormattedDate = null;
      this.$refs.datePickerMenu.save(this.vValue);
      this.$emit('input', this.vValue);
    },
    // locale   datepicker  
    setLocalFormattedDate () {
      var dateFormat = 'L';
      if (this.locale.toLowerCase() === 'ko-kr') dateFormat = 'YYYY-MM-DD';
      this.localFormattedDate = this.$comm.moment(this.vValue).format(dateFormat);
      this.$emit('input', this.vValue);
    },
  }
};
</script>

<style>
::-webkit-input-placeholder {
   font-style: italic;
   font-size: 1rem;
}
:-moz-placeholder {
   font-style: italic;
   font-size: 1rem;  
}
::-moz-placeholder {
   font-style: italic;
   font-size: 1rem;  
}
:-ms-input-placeholder {  
   font-style: italic;
   font-size: 1rem; 
}
</style>



